import React from "react";
import "./Order.css";

const order = props => {
  let ingredients = [];
  for (let key in props.ingredients) {
    if(props.ingredients[key]) {
      ingredients.push(
        <span key={key}>
          {key}: {props.ingredients[key]}
        </span>
      );
    }
  }

  return (
    <div className="Order">
      <p>Ingredients: {ingredients}</p>
      <p>
        Price: <strong>${props.price}</strong>
      </p>
    </div>
  );
};

export default order;



